<template>
  <div id="study">
    <el-container>
      <el-main>
        <agora :user="user"/>
      </el-main>
      <el-aside>
        <video id="v"></video>

        <el-card class="box-card">
          <template #header>
            <span>‚úÖÁè≠Á∫ßË°®Áé∞ËØÑÂàÜ</span>
          </template>
          <p>80</p>
        </el-card>
        <el-card class="box-card">
          <template #header>
            <span>‚ö†Ô∏èË°®Áé∞ËæÉÂ∑ÆÁöÑÂ≠¶Áîü</span>
          </template>
          <p>ÊùéÂçé</p>
        </el-card>
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import agora from "@/components/agora";

function startCamera() {
  // ËÄÅÁöÑÊµèËßàÂô®ÂèØËÉΩÊ†πÊú¨Ê≤°ÊúâÂÆûÁé∞ mediaDevicesÔºåÊâÄ‰ª•Êàë‰ª¨ÂèØ‰ª•ÂÖàËÆæÁΩÆ‰∏Ä‰∏™Á©∫ÁöÑÂØπË±°
  if (navigator.mediaDevices === undefined) {
    navigator.mediaDevices = {};
  }
  if (navigator.mediaDevices.getUserMedia === undefined) {
    navigator.mediaDevices.getUserMedia = function (constraints) {
      // È¶ñÂÖàÔºåÂ¶ÇÊûúÊúâgetUserMediaÁöÑËØùÔºåÂ∞±Ëé∑ÂæóÂÆÉ
      var getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;

      // ‰∏Ä‰∫õÊµèËßàÂô®Ê†πÊú¨Ê≤°ÂÆûÁé∞ÂÆÉ - ÈÇ£‰πàÂ∞±ËøîÂõû‰∏Ä‰∏™errorÂà∞promiseÁöÑrejectÊù•‰øùÊåÅ‰∏Ä‰∏™Áªü‰∏ÄÁöÑÊé•Âè£
      if (!getUserMedia) {
        return Promise.reject(new Error('getUserMedia is not implemented in this browser'));
      }

      // Âê¶ÂàôÔºå‰∏∫ËÄÅÁöÑnavigator.getUserMediaÊñπÊ≥ïÂåÖË£π‰∏Ä‰∏™Promise
      return new Promise(function (resolve, reject) {
        getUserMedia.call(navigator, constraints, resolve, reject);
      });
    }
  }
  const constraints = {
    video: true,
    audio: false
  };
  let promise = navigator.mediaDevices.getUserMedia(constraints);
  promise.then(stream => {
    let v = document.getElementById('v');
    // ÊóßÁöÑÊµèËßàÂô®ÂèØËÉΩÊ≤°ÊúâsrcObject
    if ("srcObject" in v) {
      v.srcObject = stream;
    } else {
      // Èò≤Ê≠¢ÂÜçÊñ∞ÁöÑÊµèËßàÂô®Èáå‰ΩøÁî®ÂÆÉÔºåÂõ†‰∏∫ÂÆÉÂ∑≤Áªè‰∏çÂÜçÊîØÊåÅ‰∫Ü
      v.src = window.URL.createObjectURL(stream);
    }
    v.onloadedmetadata = function () {
      v.play();
    };
  }).catch(err => {
    console.error(err.name + ": " + err.message);
  })
}

export default {
  name: "study",
  props: ["user"],
  components: {
    agora
  },
  mounted() {
    // Ë∞ÉÁî®ÊëÑÂÉèÂ§¥
    startCamera();
  }
}
</script>

<style scoped>
#study {
  flex: 1;
}

.el-container {
  height: 100%;
}

.el-aside {
  background-color: #d3dce6;
  text-align: center;
  line-height: 60px;
  width: 20%;
  height: 100%;
}

.el-main {
  background-color: #e9eef3;
  text-align: center;
  line-height: 60px;
  padding: 0px;
}

video {
  width: 100%;
}

.el-card {
  margin: 20px;
}

.el-card span {
  font-size: x-large;
  margin: 0px;
}

.el-card p {
  font-size: xx-large;
}
</style>